import utils from '@/utils/index';

const { calcTwoObjectEqual } = utils; // 

const config = {
  host: '127.0.0.1',
  port: 3000,
};
class MyWebSocket {
  isReconnect = true; // 
  // 
  config = {}
  // websoket
  ws = null;
  //  
  messageList = []
  constructor(params = {}) {
    this.config = params;
    this.init();
  }
  // 
  /**
   * (options: {
        code: '1', // code
        // 
      }
      callback: () =>{})
   */
  addMessageQueue(params = {}) {
    const already = this.messageList.some(item => calcTwoObjectEqual(item.options, params.options)); // 
    if (!already) { // 
      this.messageList.push(params);
    }
  }

  send(data) {
    const sendData = typeof data !== 'string' ? JSON.stringify(data) : data;
    this.ws.send(sendData);
  }
  // 
  close() {
    this.isReconnect = false;
    this.ws.close();
    this.ws = null;
  }
  // 
  open() {
    this.isReconnect = true;
    this.init();
  }

  // 
  init() {
    this.connection();
    this.onMessage();
    this.onClose();
    this.onError();
  }
  connection() {
    const host = this.config.host || '127.0.0.1';
    const port = this.config.port || '3000';
    this.ws = new WebSocket(`ws://${host}:${port}`);
  }
  onMessage() {
    if (this.ws) {
      this.ws.onmessage = (res) => {
        try {
          console.log(1, res.data, typeof res.data);
          const resObj = JSON.parse(res.data);
          console.log(1);
          // code, 
          const messageEvent = this.messageList.find(item =>
            //  resObj.options messageList=> item.options
            calcTwoObjectEqual(item.options, resObj.options),
          );
          console.log(1, messageEvent);

          if (messageEvent) {
            messageEvent.callback(resObj.data);
          } else {
            console.error('code:', resObj);
          }
        } catch (error) {
          console.error(`_onMessage${new Error(error)}`);
        }
      };
    }
  }
  onClose() {
    this.ws.onclose = function () {
      if (this.isReconnect) {
        this.connection();
      }
    };
  }
  onError() {
    this.ws.onerror = function (error) {
      console.error('ws', new Error(error));
    };
  }
}

export default new MyWebSocket(config);


